* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Login Page */

.login-main {
  display: grid;
  margin-top: 75px;
}

.login-container {
  display: grid;
  grid-template-columns: 26% 20%;
  justify-content: center;
}

.login-left {
  text-align: center;
}

.login-left img {
  width: 450px;
}

.login-right {
  display: grid;
  padding-bottom: 10px;
  text-align: center;
  align-content: space-evenly;
}

.login-right img {
  width: 180px;
  padding-bottom: 15px;
}

.form-group input {
  width: 250px;
  height: 30px;
  margin: 5px;
  background-color: #fafafa;
  border: 1px solid #dbdbdb;
}

.login-main button {
  margin-top: 5px;
  width: 250px;
  height: 30px;
  background-color: #bde0fc;
  border: 1px solid #bde0fc;
  border-radius: 5px;
  color: white;
  font-weight: bold;
}

.app-box img {
  width: 40%;
  padding: 5px;
}

.form-box {
  padding: 20px 10px 30px 10px;
  border: 1px solid #dbdbdb;
  background-color: white;
}

.form-box a {
  text-decoration: none;
  font-size: 13px;
  font-weight: bold;
  color: #385185;
}

.form-box .loginWithFB {
  color: #385185;
  font-weight: bold;
  display: inline-flex;
  align-content: center;
  font-size: 15px;
}

.form-box .loginWithFB img {
  width: 18px;
  padding-right: 5px;
}

.signup-box {
  padding: 10px;
  border: 1px solid #dbdbdb;
  background-color: white;
}

.signup-box a {
  color: #0998f5;
  text-decoration: none;
  font-weight: bold;
}

/* Feed  */

.feed-main {
  justify-content: center;
}

.feed-container {
  text-align: center;
  width: 100%;
}

.fixed-nav {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 80px;
  width: 100%;
}

.feed-nav {
  margin-left: 20%;
  margin-right: 20%;
  display: grid;
  grid-template-columns: 30% 30% 30%;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border: 1px solid #dbdbdb;
  margin-bottom: 25px;
}

.feed-nav .nav-left {
  grid-column: 1 / span 1;
  text-align: left;
}

.feed-nav .nav-left img {
  width: 130px;
  padding: 5px;
}

.feed-nav .nav-middle {
  grid-column: 2 / span 1;
}

.feed-nav .nav-middle input {
  width: 250px;
  height: 30px;
  margin: 5px;
  background-color: #fafafa;
  border: 1px solid #dbdbdb;
  text-align: center;
  border-radius: 5px;
}

.feed-nav .nav-right {
  grid-column: 3 / span 1;
  text-align: right;
}

.feed-nav .nav-right img {
  width: 35px;
  padding: 5px;
}

.feed {
  display: grid;
  margin-top: 80px;
  width: 100%;
  grid-template-columns: 40% 20%;
  margin-left: 20%;
  margin-right: 20%;
}

/* Posts */

.post {
  border: 1px solid #dbdbdb;
  background-color: white;
  margin-bottom: 10px;
  padding: 10px 0px 10px 0px;
}

.post img {
  width: 100%;
}

.userInfo {
  display: flex;
  align-items: center;
  text-align: left;
  margin: 3px 0px 10px 3px;
}

.userInfo img {
  width: 45px;
  border-radius: 50px;
  margin-right: 10px;
}

/* Reactions */

.stats {
  margin: 5px;
  text-align: left;
  font-size: 15px;
}

.reactions {
  display: flex;
}

.reactions img {
  width: 35px;
  padding-left: 5px;
  padding-right: 5px;
}

.reactions-left {
  width: 50%;
  text-align: left;
}

.reactions-right {
  width: 50%;
  text-align: right;
}

.addComment {
  margin-top: 20px;
  text-align: center;
}

.addComment input {
  width: 85%;
  height: 25px;
}

.addComment button {
  background-color: white;
  border: 1px solid #dbdbdb;
  margin-left: 20px;
  color: #0998f5;
  font-weight: bold;
}

.comment-added {
  margin-top: 5px;
  margin-bottom: 5px;
}

.comment-added img {
  width: 20px;
}

.recommended {
  text-align: left;
  margin-left: 20px;
}

.recommended img {
  width: 50px;
  border-radius: 50px;
  border: 1px solid #dbdbdb;
  margin-right: 10px;
}

.recommended .usernameImg {
  display: flex;
  margin-bottom: 5px;
}

a {
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  color: #262626;
  margin-right: 5px;
}
